*
{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
body
{

  font-family: 'Kodchasan', sans-serif;
}
.navbarList
{
  background-color: rgba(0, 0, 0, 0.438);
  color: white;
  position: fixed;
  top:0px;
  width:100%;
  overflow: hidden;
  z-index: 10;
  backdrop-filter: blur(10px);
}
.nav-logo
{
  position: absolute;
  top:6px;
  left: 30px;
  font-size: 25px;
}
.m-bar
{
  position: absolute;
  top:8px;
  right: 30px;
  font-size: 25px;
}
.navul
{
  text-align: center;
  padding-top: 10px;
  overflow: hidden;
}
.nav-item
{
  display: inline-block;
 text-align: center;
 margin-left: 60px;
}
.nav-item a
{
  color: white;
}

#home
{
  
  background-color: black;
  width:100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow: hidden;
}
.home-content
{
  z-index: 5;
  text-align: center;
}
.home-content h1
{
  font-size: 7vw;
  color: white;
  text-shadow: 5px 5px #fed049;
}
.home-bg-img
{
  position: absolute;
  top:60%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 3;
}
.bg-circle
{
  width:700px;
  height: 700px;
  background-color: #f05454;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  position: absolute;
  border-radius: 50%;
  z-index: 2;
  animation: changecolor 5s infinite
}
@keyframes changecolor
{
  0%
  {
    background-color: #fed049;
  }
  25%
  {
    background-color: #9f5f80;
  }
  50%
  {
    background-color: #583d72;
  }
  75%
  {
    background-color: #4ca1a3;
  }
  100%
  {
    background-color: #f1e189;
  }
}
.bg-circle2
{
  width:1200px;
  height: 1200px;
  background-color: #903749;
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 50%;
  z-index: 1;
}
.home-content a
{
  color:black;
  padding: 10px 80px;
 background:linear-gradient(90deg,white,#fed049,white);
  transition: .5s ease-in-out;
  box-shadow: 5px 5px #fed049;
  background-size: 800%;
  
}
.home-content a:hover
{
  animation: bgchange 5s infinite;
}
@keyframes bgchange
{
  0%
  {
    background-position: 0%;
  }
  100%
  {
    background-position: 400%;
  }
}


.home-bg-img img
{
  height: 600px;
}

#vr
{
  width:100%;
  height: 100vh;
  padding: 50px;
  position: relative;
  background-color: black;
  z-index: 2;

}
.sec1-img,.sec1-text
{
  margin: 20px;
}
#vr .row
{
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}
.sec1-text h1
{
  font-size: 5vw;
  text-shadow: 5px 5px #903749;
}
.sec1-text a
{
  background: white;
  color: black;
  box-shadow: 5px 5px #903749;
  padding: 10px 30px;
  margin-right: 10px;
}
.bg-text
{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: -1;
  color: rgba(15, 15, 15, 0.151);
  width: 100%;
  text-align: center;
}
.bg-text h1
{
  font-size: 12vw
}

#experience
{
  background: url("D:\My Project\app3\src\Component\Images\space2.jpg") no-repeat;
  background-size: cover;
  width:100%;
  height:100vh;
  position: relative;
  background-position: top;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
#experience .container
{
  z-index: 3;
}
.sec3-bg-img
{
  position: absolute;
  width:75vw;
  height: 55vw;
  right:0px;
  bottom: 0px;
  z-index: 2;
}
.sec3-text h1
{
  font-size: 5vw;
  color: white;
  text-shadow: 5px 5px  black;
}
.sec3-text a
{
  background-color: white;
  color: black;
  padding:10px 40px;
  transition: .5s ease-in-out;
  box-shadow: 5px 5px black;
}
#section4
{
  width:100%;
  height: 100vh;
  position: relative;
  background: url("D:\My Project\app3\src\Component\Images\space.jpg")no-repeat;
  background-size: cover;
  overflow: hidden;
 
}
.sec4-text
{
  background: url("D:\My Project\app3\src\Component\Images\space.jpg")no-repeat;
  background-size: cover;
  width:100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  text-align: center;
  
}
.sec4-text:nth-child(1)
{
  filter:blur(10px)
}
.sec4-circle
{
  width:300px;
  height: 300px;
  border-radius: 50%;
  border:3px solid white;
  position: absolute;
  top:-150px;
  left: -150px;
  pointer-events: none;
}
.sec4-text a
{
  color:white;
  border:3px solid white;
  padding:10px 40px;
  transition: .5s ease-in-out;
  box-shadow: 5px 5px rgb(21, 4, 173);
}
.sec4-text a:hover
{
  background-color: white;
  color: black;
  cursor: pointer;
}

.sec4-text h1
{
  font-size: 10vw;
  color: white;
  text-shadow: 5px 5px  rgb(21, 4, 173);
}

#galary
{
  width:100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white;
  padding: 30px;
}
.sec5-container
{
  display: grid;
  justify-content: center;
  grid-template-columns: auto auto auto;
  align-items: center;

}
.sec5-ct
{
  width:350px;
  height: 350px;
  background-color: red;
  margin: 10px;
  position: relative;
}
.sec5-ct:nth-child(1)
{
  grid-row: 1/3;
  height: 720px;
  background: url("D:\My Project\app3\src\Component\Images\kid.jpg")no-repeat;
  background-size: cover;
  background-position: center;
}
.sec5-ct:nth-child(2)
{
  background: url("https://image.freepik.com/free-photo/man-with-futuristic-device-pointing-up_23-2148864988.jpg")no-repeat;
  background-size: cover;
  background-position: center;
  grid-column: 2/4;
  width:auto;

}
.sec5-ct:nth-child(3)
{
  background: url("https://image.freepik.com/free-photo/man-using-playing-with-vr-glasses_1303-26852.jpg")no-repeat;
  background-size: cover;
  background-position: center;
}
.sec5-ct:nth-child(4)
{
  background: url("https://image.freepik.com/free-photo/top-view-virtual-reality-headset-white-headphones_23-2148912738.jpg")no-repeat;
  background-size: cover;
  background-position: center;
}
.sec5-ct:hover .sec5-text
{
  width: 100%;
  height: 100%;
}
.sec5-text h4
{
  text-shadow:  5px 5px #903749;
}
.sec5-text
{
 position: absolute;
  background-color: rgba(0, 0, 0, 0.507);
  width:0%;
  height: 0%;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  overflow: hidden;
  transition: .5s ease-in-out;

}
.sec5-text div
{
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%);
  color: white;
  text-align: center;
}

#design
{
  width:100%;
  height: auto;
  position: relative;
}
.sec6-title h1
{
  text-shadow: 2px 2px rgb(247, 164, 55);
}
.sec6-img
{
  text-align: center;
  
}
.sec6-title
{
  text-align: center;
  padding-top:80px;
}
.sec6-img img
{
  width:1000px;
  height: 600px;
}
#section7
{
  background: url("D:\My Project\app3\src\Component\Images\gear.jpg")no-repeat;
  background-size: cover;
  width: 100%;
  height: 50vw;
  position: relative;
  background-color: black;
}
.sec8-title h1
{
  text-shadow: 2px 2px yellowgreen;
}
.carousel-item img
{
  height: 40vw;
  width: 100%;
  
}
.sec8-title
{
  text-align: center;
  padding:50px;
  
}
#register
{
  padding: 50px;
}
.sec9-container
{
  display: flex;
  justify-content: center;
  align-items: center;
}
.sec9-content
{
  width: 600px;
  height: 600px;
}
.sec9-content:nth-child(1)
{
  background: url("D:\My Project\app3\src\Component\Images\4714964.jpg")no-repeat;
  background-size: cover;
  background-position: center;
 
}
.sec9-content:nth-child(2)
{
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
}

footer
{
  background-color: rgb(24, 23, 23);
  width:100%;
  height:auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 30px;
  color: white;
  text-align: center;
}
.links li,.follow li
{
  display: inline-block;
  margin-left: 30px;
  text-align: center;
  
}
.links a , .follow a
{
  color: white;
}











@media screen and (max-width:950px)
{
  .nav-item
  {
    margin-left: 50px;
  }
  .sec5-ct
  {
    width:250px;
    height: 250px;
  }
  .sec5-ct:nth-child(1)
  {
    height: 520px;
  }
  .sec6-img img
  {
    width:600px;
    height: 500px;
  }
  .sec9-container
  {
    flex-direction: column;
  }
  .sec9-content
  {
    width:100%;
    padding: 20px;
  }
  .sec9-content:nth-child(1)
  {
    height: 250px;
  }
  .sec9-content:nth-child(2)
  {
    height: auto;
  }

}

@media screen and (max-width:850px)
{
  .navbarList
  {
    padding-top: 30px;
  }
  #vr
  {
    height: auto;
  }
  .bg-circle
  {
    width: 400px;
    height: 400px;
  }
  .bg-circle2
  {
    width: 600px;
    height: 600px;
  }
  .menu-bar
  {
    display: block;
  }
  .home-bg-img
  {
    top:65%;
  }
  .home-bg-img img
  {
    height: 400px;
  }
  .sec5-container
  {
    grid-template-columns: auto auto;
  }
  .sec5-ct:nth-child(1)
  {
    height: 250px;
    grid-row: 1/1;
  }
  .sec5-ct:nth-child(2)
  {
    grid-column: 2/2;
  }
  .nav-item
  {
    display: block;
    margin-left: 0px;
    margin-top: 25px;
    
  }
  footer
  {
    flex-direction: column;
  }
  .links li , .follow li
  {
    display: block;
    margin: 0px;
    line-height: 25px;
  }
}

@media screen and (max-width:650px)
{
  .bg-circle
  {
    width: 300px;
    height: 300px;
  }
  .bg-circle2
  {
    width: 500px;
    height: 500px;
  }
  .home-bg-img
  {
    top:65%;
  }
  #vr,#section2,#experience  {
    padding: 10px;
  }
  #vr a
  {
    padding: 10px 20px;
  }
  #experience
  {
    height: 400px;
  }
  .sec5-container
  {
    grid-template-columns: auto;
  }
  .sec5-ct:nth-child(1),.sec5-ct:nth-child(2)
  {
    grid-row: auto;
    grid-column:auto;
    
  }
  .sec5-ct
  {
    width:250px;
    height: 250px;
  }
  .sec6-img img
  {
    width:100%;
    height: 300px;
  }
  
 

}